.formControl{font-family:"Inter","Jost",sans-serif;width:100%;text-align:left;margin:0 auto 20px auto}.formControl input{cursor:pointer}.formControl input[type=text]{border:none;border-bottom:1px solid #000;font-size:14px;font-weight:400;color:#000;padding:0 0 5px 0;width:100%;background:rgba(0,0,0,0);outline:none}.formControl input[type=text]:focus::placeholder{opacity:0;transition:.2s ease}.formControl input[type=text]::placeholder{color:inherit;font-size:inherit;opacity:1;transition:.2s ease}.formControl input[type=checkbox]{display:none}.formControl input[type=checkbox]+label{position:relative;font-size:14px;line-height:17px;font-weight:400;padding:0 0 0 33px;width:100%;text-align:left;display:flex;flex-wrap:wrap}.formControl input[type=checkbox]+label::before{content:"";position:absolute;left:0;top:calc(0% - 3px);border:1px solid #000;width:23px;height:23px}.formControl input[type=checkbox]:checked+label::before{content:"✓";font-size:20px;line-height:20px;text-align:center;font-weight:400;color:#000}.formControl input[type=checkbox]+label+span{padding-left:33px}.formControl.error input,.formControl.error textarea{border-bottom:1px rgba(219,16,16,.856) solid}.formControl.error span{display:inline-block;color:rgba(219,16,16,.856);font-size:14px}.formSummaryError{border:1px solid rgba(219,16,16,.856);padding:5px;color:rgba(219,16,16,.856)}