@import "../../../../assets/styles/variables.scss";

.preLoaderWrapper {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;

  img {
    position: absolute;
    top: 50%;
    bottom: 0;
    right: 0;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
